import { Controller } from '@ali/4ever-cangjie';
import React from 'react';
import { isKeyHotkey } from 'is-hotkey';
import increaseIndent from '../commands/increaseIndent';
import decreaseIndent from '../commands/decreaseIndent';
import onTab from './onTab';
import onDelete from './onDelete';
import { IndentTrigger } from '../utils';

const isTabHotKey = isKeyHotkey('shift?+tab');
const isBackspaceHotKey = isKeyHotkey('backspace');
const isDeleteHotKey = isKeyHotkey('delete');
const isIncreaseHotKey = isKeyHotkey('mod+]');
const isDecreaseHotKey = isKeyHotkey('mod+[');

export default function onKeyDown(event: React.KeyboardEvent, editor: Controller, next: Function) {
  if (isTabHotKey(event)) {
    return onTab(event, editor, next);
  }

  if (isBackspaceHotKey(event) || isDeleteHotKey(event)) {
    return onDelete(event, editor, next);
  }

  if (isIncreaseHotKey(event)) {
    event.preventDefault();
    return increaseIndent(editor, IndentTrigger.toolbar);
  }

  if (isDecreaseHotKey(event)) {
    event.preventDefault();
    return decreaseIndent(editor, IndentTrigger.toolbar);
  }

  return next();
}
